input[type="text"] ~ .app-input-label, input[type="number"] ~ .app-input-label {
  position: absolute;
  top: 14px;
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.541176);
  -webkit-transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
  transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
}
input[type="text"]:focus ~ .app-input-label, input[type="text"]:valid ~ .app-input-label,
input[type="number"]:focus ~ .app-input-label, input[type="number"]:valid ~ .app-input-label {
  position: absolute;
  top: 0;
  font-size: 14px;
  color: #3f51b5;
}
input[type="radio"] {
  height: 0.0rem;
}
input, label, select, option {
  font-family: 'Baskerville, "Baskerville Old Face", "Goudy Old Style", Garamond, "Times New Roman", serif !important';
