}
.app-fade {
  opacity: 1;
  -webkit-animation: fade .3s ease-in-out;
          animation: fade .3s ease-in-out;
}
.page {
  background-color: #f0e009;
  border-color: #ffdd33;
  height: calc(100vh - 52px);
  position: relative;
  /** next 3 props compensate for toolbar **/
  padding-bottom: 15px;
  box-sizing: border-box;
  font-family: inherit;
}
.page-will-change {
  will-change: opacity;
}
.page-footer-fixed {
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0px;
  height: 40px;
  width: 100%;
  background-color: #f0e009;
  padding-top: 2px;
  padding-bottom: 2px;
}
.hidden {
  display: none;
}
@-webkit-keyframes fade {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fade {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@media (max-device-width: 500px) {
.app-button-text {
    display: none;
}
button {
    padding-top: 5px;
    padding-bottom: 5px;
}
}
@media (min-device-width: 800px) {
.app-hide-big-screen {
    display: none !important;
}
.app-hide-small-screen {
    display: initial !important;
}
}



